:root {
  --text-color: #000000;
  --text-color-light: #ffffff;
  --text-body: #444444;
  --no-image-color: #fafafa;
  --accent-color: #63d1bb;
  --accent-color-dark: #62d1ba;
  --bg-color: #f1f1f1;
  --icon-color: #231f20;
  --bg: #ffffff;
  --bg-menu: #fafafa;
  --border-light: #ececec;
  --border-footer: #e5e5e5;
}

@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2");
  font-display: swap;
}
img {
  max-width: 100%;
  object-fit: cover;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 13px;
  line-height: 30px;
  color: var(--text-body);
  min-height: 100vh;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}

.page__body {
  min-width: 320px;
  margin: 0;
}

.button {
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
  color: var(--text-body);
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  box-sizing: border-box;
  display: block;
  padding-top: 16px;
  padding-bottom: 16px;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}
.button--primary {
  border: 2px solid var(--text-body);
  background-color: var(--text-color-light);
}

.header {
  min-height: 85px;
  position: relative;
}

.main-nav {
  display: flex;
  position: relative;
  flex-direction: column;
  background-color: var(--bg);
}
@media (min-width: 768px) {
  .main-nav {
    display: grid;
    grid-template-columns: 50px 1fr 1fr 85px max-content 50px;
    background-color: var(--bg-menu);
  }
}
@media (min-width: 1150px) {
  .main-nav {
    grid-template-columns: 50px max-content max-content 1fr 85px max-content 50px;
  }
}
@media (min-width: 768px) {
  .main-nav::before {
    content: "";
    display: block;
    grid-column: 1/2;
    width: 100%;
    height: 100%;
    background-color: var(--bg);
    box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .main-nav::before {
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 768px) {
  .main-nav::after {
    content: "";
    display: block;
    grid-column: 6/7;
    width: 100%;
    height: 100%;
    background-color: var(--bg);
    box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .main-nav::after {
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 1150px) {
  .main-nav::after {
    grid-column: 7/8;
    grid-row: 1/2;
  }
}
@media (min-width: 768px) {
  .main-nav--notindex {
    background-color: var(--bg-color);
  }
}

.main-nav__toggle {
  border: none;
  background-image: url("../img/icons/menu-close.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-color: transparent;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 30px;
  right: 30px;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav__wrapper {
  display: flex;
  flex-direction: column;
  position: absolute;
  z-index: 10;
  width: 100%;
  top: 85px;
  background-color: var(--bg);
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    display: contents;
  }
}

.main-nav--closed .main-nav__toggle {
  background-image: url("../img/icons/burger-menu.svg");
}

.main-nav--closed .main-nav__wrapper {
  display: none;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__wrapper {
    display: contents;
  }
}

.no-js .main-nav__toggle {
  display: none;
}

.no-js .main-nav__wrapper {
  display: flex;
  position: relative;
  top: 0;
}
@media (min-width: 768px) {
  .no-js .main-nav__wrapper {
    display: contents;
  }
}

.main-nav__logo {
  padding-left: 30px;
  padding-right: 30px;
}
@media (min-width: 768px) {
  .main-nav__logo {
    grid-column: 2/3;
    grid-row: 1/2;
    justify-content: start;
    padding-left: 0;
    padding-right: 0;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo {
    grid-column: 4/5;
  }
}

.main-nav__caption {
  display: none;
  font-size: 15px;
  line-height: 24px;
  color: var(--text-body);
}
@media (min-width: 768px) {
  .main-nav__caption {
    grid-column: 5/6;
    grid-row: 2/3;
    display: block;
    text-align: right;
    align-self: center;
  }
}
@media (min-width: 1150px) {
  .main-nav__caption {
    grid-column: 6/7;
  }
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 25px;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media (min-width: 768px) {
  .logo {
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 768px) {
  .logo {
    background-color: var(--bg);
    justify-content: start;
  }
}
@media (min-width: 1150px) {
  .logo {
    padding-right: 50px;
    justify-content: center;
  }
}

.site-nav {
  list-style: none;
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .site-nav {
    grid-column: 2/4;
    grid-row: 2/3;
    flex-direction: row;
    gap: 30px;
  }
}
@media (min-width: 1150px) {
  .site-nav {
    grid-column: 2/4;
    grid-row: 1/2;
    flex-direction: row;
    gap: 0;
    background-color: var(--bg);
    box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
  }
}
@media (min-width: 1150px) and (min-width: 768px) {
  .site-nav {
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  }
}

.site-nav__item {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
  padding-left: 30px;
  padding-right: 30px;
}
@media (min-width: 768px) {
  .site-nav__item {
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 768px) {
  .site-nav__item {
    padding-left: 0;
    padding-right: 0;
    justify-content: start;
    box-shadow: none;
  }
}

@media (min-width: 1150px) {
  .site-nav__item:first-child .site-nav__link {
    padding-left: 0;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
  }
}

.site-nav__link {
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
  color: var(--text-color);
  text-decoration: none;
  display: block;
  padding-top: 30px;
  padding-bottom: 30px;
  width: 215px;
  padding-left: 45px;
}
@media (min-width: 768px) {
  .site-nav__link {
    font-size: 15px;
    padding-top: 20px;
    padding-bottom: 20px;
    width: max-content;
    padding-left: 0;
    padding-right: 0;
  }
}
@media (min-width: 1150px) {
  .site-nav__link {
    font-size: 17px;
    padding-top: 30px;
    padding-bottom: 30px;
    padding-left: 35px;
    padding-right: 35px;
  }
}

.search {
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
  padding-left: 30px;
  padding-right: 30px;
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .search {
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 768px) {
  .search {
    background-color: var(--bg);
    grid-column: 3/5;
    grid-row: 1/2;
    justify-content: end;
    padding: 0;
  }
}
@media (min-width: 1150px) {
  .search {
    grid-column: 5/6;
  }
}

.search__button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: none;
  position: relative;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-left: 0;
  padding-right: 0;
}
@media (min-width: 768px) {
  .search__button {
    width: 85px;
    height: 85px;
    padding-left: 0;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    border-right: 1px solid rgba(0, 0, 0, 0.1);
  }
}

.search__label {
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
  color: var(--text-color);
  text-align: left;
  display: block;
  width: 215px;
  padding-left: 45px;
}
@media (min-width: 768px) {
  .search__label {
    display: none;
  }
}

.search__icon {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
}
@media (min-width: 768px) {
  .search__icon {
    position: relative;
  }
}

.search__popup {
  display: none;
}

.cart {
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
  color: var(--text-color);
  text-decoration: none;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
  padding-left: 30px;
  padding-right: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 30px;
  padding-bottom: 30px;
}
@media (min-width: 768px) {
  .cart {
    box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 768px) {
  .cart {
    background-color: var(--bg);
    grid-column: 5/6;
    grid-row: 1/2;
    padding-left: 0;
    padding-right: 0;
    justify-self: end;
  }
}
@media (min-width: 1150px) {
  .cart {
    grid-column: 6/7;
  }
}

.cart__label {
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
  color: var(--text-color);
  text-align: left;
  display: block;
  position: relative;
  padding-left: 45px;
  width: 215px;
}
@media (min-width: 768px) {
  .cart__label {
    width: auto;
    padding-left: 85px;
    padding-right: 0;
  }
}

.cart__icon {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
}
@media (min-width: 768px) {
  .cart__icon {
    left: 35px;
  }
}

.additional-nav {
  display: none;
  list-style: none;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
}
@media (min-width: 1150px) {
  .additional-nav {
    display: flex;
    gap: 60px;
    grid-column: 2/4;
  }
}

.additional-nav__item {
  display: flex;
  align-items: center;
}

.additional-nav__link {
  font-size: 15px;
  font-weight: 700;
  line-height: 24px;
  color: var(--text-color);
  text-decoration: none;
  display: block;
  padding-top: 15px;
  padding-bottom: 15px;
  width: 100%;
}

.main {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .main--page {
    display: grid;
    grid-template-columns: 50px 1fr 50px;
  }
}

.main__title {
  font-size: 27px;
  font-weight: 700;
  line-height: 30px;
  color: var(--text-color);
  text-align: center;
  background-color: var(--bg-color);
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 75px;
  padding-bottom: 75px;
}
@media (min-width: 768px) {
  .main__title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .main__title {
    font-size: 53px;
    line-height: 57px;
  }
}

@media (min-width: 768px) {
  .main--page .main__title {
    grid-column: 1/4;
    display: grid;
    line-height: 180px;
    padding: 0;
  }
}
@media (min-width: 768px) {
  .main--page .main__title::after {
    content: "";
    display: block;
    align-self: end;
    height: 24px;
    background-image: url("../img/common/zigzag-fill-tablet.svg");
    background-repeat: repeat-x;
    background-position: 50% 13px;
  }
}
@media (min-width: 1150px) {
  .main--page .main__title::after {
    background-image: url("../img/common/zigzag-fill-desktop.svg");
    height: 34px;
    background-position-y: 19px;
  }
}

.hero {
  display: grid;
  background-color: var(--no-image-color);
  background-image: url("../img/common/hero-mobile@1x.jpg");
  background-position: 50% -25px;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .hero {
    grid-template-columns: 1fr 1fr;
    background-image: url("../img/common/hero-desktop.jpg");
    background-position-y: -50px;
  }
}
@media (min-width: 768px) {
  .hero::after {
    content: "";
    grid-row: 2/3;
    grid-column: 1/3;
    display: block;
    align-self: end;
    height: 24px;
    background-image: url("../img/common/zigzag-fill-tablet.svg");
    background-repeat: repeat-x;
    background-position: 50% 13px;
  }
}
@media (min-width: 1150px) {
  .hero::after {
    background-image: url("../img/common/zigzag-fill-desktop.svg");
    height: 34px;
    background-position-y: 19px;
  }
}

.hero__title {
  font-size: 27px;
  font-weight: 700;
  line-height: 30px;
  color: var(--text-color);
  text-align: center;
  justify-self: center;
  box-sizing: border-box;
  margin-top: 0;
  margin-bottom: 0;
  padding: 70px 30px 120px;
  min-height: 280px;
}
@media (min-width: 768px) {
  .hero__title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .hero__title {
    font-size: 53px;
    line-height: 57px;
  }
}
@media (min-width: 768px) {
  .hero__title {
    grid-column: 1/3;
    grid-row: 1/2;
    max-width: 700px;
    padding-top: 90px;
    min-height: 560px;
  }
}

.hero__item--light {
  background-color: #63d1bb;
}
@media (min-width: 768px) {
  .hero__item--light {
    grid-column: 1/2;
    grid-row: 2/3;
    margin-left: 50px;
    margin-right: 0;
  }
}
.hero__item--dark {
  background-color: #56c3b2;
}
@media (min-width: 768px) {
  .hero__item--dark {
    grid-column: 2/3;
    grid-row: 2/3;
    margin-right: 50px;
    margin-left: 0;
  }
}

.hero__link {
  font-size: 19px;
  font-weight: 700;
  line-height: 24px;
  color: var(--text-color-light);
  text-decoration: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
}
@media (min-width: 768px) {
  .hero__link {
    font-size: 20px;
    line-height: 30px;
    padding: 25px 35px;
  }
}
@media (min-width: 1150px) {
  .hero__link {
    grid-template-columns: repeat(3, 1fr);
  }
}
.hero__link::after {
  content: "";
  display: block;
  width: 62px;
  height: 62px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  opacity: 0.5;
  justify-self: end;
}
@media (min-width: 1150px) {
  .hero__link::after {
    grid-column: 3/4;
  }
}
.hero__link--interior::after {
  background-image: url("../img/common/interior.svg");
}
.hero__link--toy::after {
  background-image: url("../img/common/toy.svg");
}

.product {
  display: grid;
  grid-template-columns: 30px 1fr 30px;
  position: relative;
}
@media (min-width: 768px) {
  .product {
    grid-template-columns: 50px repeat(4, 1fr) 50px;
    grid-template-rows: repeat(5, min-content);
    padding-bottom: 70px;
    padding-top: 45px;
  }
}
@media (min-width: 1150px) {
  .product {
    grid-template-columns: 50px repeat(6, 1fr) 50px;
    padding-top: 70px;
    padding-bottom: 90px;
  }
}
.product::after {
  content: "";
  display: block;
  position: absolute;
  height: 6px;
  width: 100%;
  background-image: url("../../img/common/zigzag-line-desktop.svg");
  background-repeat: repeat-x;
  background-size: contain;
  bottom: 0;
}
@media (min-width: 768px) {
  .product::after {
    height: 17px;
    grid-column: 1/7;
  }
}
@media (min-width: 1150px) {
  .product::after {
    height: 23px;
    grid-column: 2/8;
  }
}

.product__title {
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
  color: var(--text-color);
  grid-column: 2/3;
}
@media (min-width: 768px) {
  .product__title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .product__title {
    font-size: 53px;
    line-height: 57px;
  }
}
@media (min-width: 768px) {
  .product__title {
    grid-column: 2/5;
    grid-row: 1/2;
    margin-top: 0;
    margin-bottom: 35px;
  }
}
@media (min-width: 1150px) {
  .product__title {
    grid-column: 3/6;
    z-index: 10;
    margin-top: 60px;
    margin-bottom: 35px;
  }
}

.product__caption {
  grid-column: 2/3;
  font-size: 17px;
  font-weight: 700;
  line-height: 20px;
  color: var(--accent-color);
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .product__caption {
    grid-column: 5/6;
    justify-self: end;
    align-self: center;
    width: min-content;
    text-align: center;
    padding-right: 30px;
    padding-left: 30px;
  }
}
@media (min-width: 1150px) {
  .product__caption {
    grid-column: 2/3;
    justify-self: center;
  }
}

.product__description {
  grid-column: 2/3;
  font-size: 15px;
  line-height: 24px;
  margin-top: 0;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .product__description {
    grid-column: 2/4;
    grid-row: 2/3;
    font-size: 17px;
    line-height: 30px;
    margin-right: 40px;
    margin-bottom: 35px;
  }
}
@media (min-width: 1150px) {
  .product__description {
    grid-column: 3/5;
    margin-right: 55px;
    margin-bottom: 30px;
  }
}

.product__list {
  grid-column: 2/3;
  display: grid;
  grid-template-columns: 30% 1fr;
  margin-top: 0;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .product__list {
    grid-column: 2/4;
    grid-row: 3/4;
    margin-bottom: 60px;
    margin-right: 40px;
  }
}
@media (min-width: 1150px) {
  .product__list {
    grid-column: 3/5;
    margin-right: 55px;
    margin-bottom: 100px;
  }
}

.product__term {
  font-size: 15px;
  line-height: 24px;
  border-bottom: 1px solid var(--border-light);
  padding-bottom: 15px;
  padding-top: 15px;
}
@media (min-width: 768px) {
  .product__term {
    line-height: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
@media (min-width: 1150px) {
  .product__term {
    padding-bottom: 20px;
  }
}

.product__value {
  font-size: 15px;
  line-height: 24px;
  border-bottom: 1px solid var(--border-light);
  padding-left: 15px;
  margin-left: 0;
  padding-bottom: 15px;
  padding-top: 15px;
}
@media (min-width: 768px) {
  .product__value {
    line-height: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
@media (min-width: 1150px) {
  .product__value {
    padding-bottom: 20px;
  }
}

