@media (min-width: 1150px) {
  .form__label-box--check {
    grid-column: 2/7;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 768px) {
  .form__label-box--fio {
    grid-template-columns: 1fr;
  }
}
@media (min-width: 1150px) {
  .form__label-box--fio {
    grid-column: 2/6;
  }
}
.form__label-box--phone {
  grid-column: 2/6;
}
.form__label-box--email {
  grid-column: 2/6;
}

.form__caption {
  font-size: 17px;
  font-weight: 700;
  line-height: 16px;
  color: var(--accent-color);
}
@media (min-width: 768px) {
  .form__caption {
    position: absolute;
    top: 0;
    left: 0;
    width: 20%;
  }
}
@media (min-width: 1150px) {
  .form__caption {
    width: 16%;
    text-align: center;
  }
}

.form__item {
  display: flex;
  flex-direction: column;
}

.form__label {
  font-size: 17px;
  font-weight: 700;
  line-height: 18px;
  color: var(--text-color);
}
.form__label--radio, .form__label--check {
  display: grid;
  grid-template-columns: 42px 1fr;
  column-gap: 20px;
  align-items: center;
  position: relative;
}
.form__label--radio::before, .form__label--check::before {
  content: "";
  display: block;
  width: 38px;
  height: 38px;
  border: 2px solid var(--text-color);
}
.form__label--radio::before {
  border-radius: 50%;
}

.form__input-radio:checked + .form__label--radio::after {
  content: "";
  display: block;
  width: 18px;
  height: 18px;
  background-color: var(--accent-color-dark);
  position: absolute;
  top: 50%;
  left: 21px;
  transform: translate(-50%, -50%);
  border-radius: 50%;
}

.form__input-check:checked + .form__label--check::after {
  content: "";
  display: block;
  width: 25px;
  height: 16px;
  position: absolute;
  top: 50%;
  left: 21px;
  transform: translate(-50%, -50%);
  background-image: url("../img/icons/tick.svg");
  background-position: 50% 50%;
}

.form__item-wrapper {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .form__item-wrapper {
    grid-column: 1/3;
  }
}
@media (min-width: 1150px) {
  .form__item-wrapper {
    grid-column: 1/4;
    flex-direction: row;
    align-items: center;
    gap: 15px;
  }
}

.form__input-text {
  font-family: inherit;
  font-size: 17px;
  line-height: 18px;
  width: 100%;
  border: none;
  box-shadow: inset 0 -2px 0 var(--text-color);
  padding: 16px 0;
}
.form__input-text::placeholder {
  color: var(--accent-color);
}

.form__input-textarea {
  font-family: inherit;
  font-size: 17px;
  line-height: 18px;
  padding: 15px;
  border: 2px solid var(--text-color);
}
@media (min-width: 768px) {
  .form__input-textarea {
    grid-column: 2/5;
  }
}
@media (min-width: 1150px) {
  .form__input-textarea {
    grid-column: 2/6;
  }
}

.form__text {
  font-size: 14px;
  line-height: 16px;
  margin-top: 0;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .form__text {
    grid-column: 2/3;
    justify-self: end;
  }
}
@media (min-width: 1150px) {
  .form__text {
    grid-column: 4/6;
  }
}

@media (min-width: 768px) {
  .form__button {
    grid-column: 1/2;
    grid-row: 8/9;
    justify-self: start;
  }
}
@media (min-width: 1150px) {
  .form__button {
    grid-column: 2/4;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  background-color: var(--bg-color);
  padding-left: 30px;
  padding-right: 30px;
}
@media (min-width: 768px) {
  .footer {
    padding-left: 50px;
    padding-right: 50px;
    flex-direction: row;
    justify-content: space-between;
  }
}

.footer__logo {
  display: none;
}
@media (min-width: 768px) {
  .footer__logo {
    display: block;
    background-color: var(--bg-color);
  }
}

.social {
  display: flex;
  justify-content: center;
  gap: 30px;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid var(--border-footer);
}
@media (min-width: 768px) {
  .social {
    border-bottom: none;
  }
}

.social__link {
  display: flex;
  width: 30px;
  height: 25px;
  justify-content: center;
  align-items: center;
  color: var(--icon-color);
  text-decoration: none;
}

.copyright {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 25px;
}

.copyright__link {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 25px;
  text-decoration: none;
}

.copyright__text {
  font-size: 19px;
  font-weight: 700;
  line-height: 18px;
  color: var(--text-color);
  text-decoration: none;
}

.copyright__logo {
  fill: var(--accent-color);
}

.modal {
  display: none;
}